import React from "react";
import MovieCard from "@/components/MovieCard";
import { fetchPopular, fetchTrending } from "@/lib/fetchData";
import { MovieType } from "@/types/global";

export default async function Home() {
  const trending = await fetchTrending();
  const popular = await fetchPopular();

  return (
    <main className="min-h-screen bg-slate-50 py-8">
      <div className="container mx-auto max-w-screen-xl px-4">
        <Section title="Trending this week" items={trending} />

        <Section title="Popular" items={popular} />
      </div>
    </main>
  );
}

/*
  Section component: small, reusable block so layout logic is kept in one place.
  - `items` expected to be an array of movie objects
  - falls back to a simple message if empty
*/
function Section({ title, items }: { title: string; items: MovieType[] }) {
  return (
    <section className="mb-10">
      <h2 className="text-2xl font-bold mb-4">{title}</h2>

      {items.length === 0 ? (
        <p className="text-sm text-slate-600">No results found.</p>
      ) : (
        <div
          className="grid gap-2 md:gap-6 grid-cols-2 sm:grid-cols-3 md:grid-cols-4 xl:grid-cols-6"
          role="list"
        >
          {items.map((movie) => (
            <div role="listitem" key={movie.id}>
              <MovieCard movie={movie} />
            </div>
          ))}
        </div>
      )}
    </section>
  );
}
